:root {
  --bs-font-family: Montserrat, Raleway;
  --bs-font-size: 16px;
  --bs-font-weight: 400;
  --bs-line-height: 1;
  --black: #000000;
  --navy: #07202F;
  --blue: #0072FF;
  --lightblue: #3A97ED;
  --yellow: #FFC357;
  --bg-black: #040B0F;
  --grey-70: rgba(255, 255, 255, 0.7);
  --grey-60: rgba(255, 255, 255, 0.6);
  --bg-white: #fff;
  --font-color: #fff;
  --radius: 15px;
  --bs-transition: 0.3s ease-in-out;
  --primary-color: #131313;
  --primary-hover-color: #2d2d2d;
  --secondary-color: #ff4040;
  --white-color: #fff;
  --black-color: #131313;
  --bs-shadow: 0px 2px 3px rgba(0, 0, 0, 0.1), 0px 8px 3px rgba(0, 0, 0, 0.1);
  --hover-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.1), 0 0 10px 0 rgba(0, 0, 0, 0.1), 0 0 15px 0 rgba(0, 0, 0, 0.1);
}

.header {
  padding: 20px 0px;
}
.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header__nav {
  flex: 1 1 auto;
  max-width: 630px;
}
.header__nav_list {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header__lang {
  display: flex;
  align-items: center;
  gap: 15px;
}
.header__lang_item {
  display: flex;
  align-items: center;
  gap: 5px;
  cursor: pointer;
}
.header__lang_item.active {
  border: 1px solid var(--font-color);
  border-radius: var(--radius);
  padding: 10px 15px;
}
.header__theme {
  cursor: pointer;
}
.header__contact {
  padding: 20px;
  color: var(--yellow);
  border: 1px solid var(--yellow);
  border-radius: var(--radius);
  cursor: pointer;
}

[class*=__container] {
  max-width: 1450px;
  margin: 0 auto;
  padding: 0px 15px;
}

body {
  background-color: var(--navy);
  color: var(--font-color);
  font-family: var(--bs-font-family);
}